.back {
    margin-top: 30px;
}
.detail-info{
    
    padding: 20px;
    border-radius: 9px;
    box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.17);
    margin-bottom: 30px;
    width: 100%;
}
.detail-info table {
    margin-top: 20px;
    margin-bottom: 50px;
    width: 100%;
    
}

.detail-info th, td {
    padding: 13px 10px;
    text-align: left;
    font-weight: 500;
}

@media (min-width: 768px){
    .card-detail {
        margin-top: 30px;
        display: flex;
    }
    .detail-info{
        width: 50%;
    }
}
