body {
  font-family: 'Montserrat' !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}

.logo-wrapper {
  margin-bottom: 20px;
}
.logo-wrapper h1{

    font-weight:600;
    background: linear-gradient(131.06deg, #00A2C8 12.5%, #1CD3FE 93.53%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 20px;
}

@media (min-width: 768px){
  .logo-wrapper {
    margin-bottom: 5px;
  }
}
.search-wrapper input {

    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background : white;
    border: none;
    border-radius: 9px;
    padding: 10px 13px;
    color: rgb(141, 141, 141);
    font-family: 'Montserrat';
}

.navbar {
    padding: 30px 0 30px;
    border-bottom: 1px solid ;
    text-align: center;
    border: 1px solid rgba(3, 173, 213, 0.2);
}

@media (min-width: 768px){
    .navbar-flex {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.container {
    width: 95%;
    margin: 0 auto;
    
}

@media (min-width: 768px) {
    .container {
        width: 90%;
        margin: 0 auto;
    }
}
@media (min-width: 992px) {
    .container {
        width: 80%;
        margin: 0 auto;
    }
}
.card, .card-detail-big{
    text-align: center;
    padding: 30px 10px;
    width: 70%;
    box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.17);
    border-radius: 16px;
    margin: 20px auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background: white;
    cursor: pointer;
}
.card-detail-big{
    width: 90%;

}

.card:hover {
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
}
.card .photo-wrapper, .card-detail-big .photo-wrapper {
    margin: 10px auto;
    width: 100px;
    height: 100px;
    overflow: hidden;
    border-radius: 50%;
}
.card .photo-wrapper img ,.card-detail-big .photo-wrapper img {
    width: 100%;

}
.card .detail .name, .card-detail-big .detail .name {
    font-size: 18px;
    font-weight: bold;
    margin: 5px 0;
    text-decoration: none;
    display: block;
    color: black
}
.card .detail .email, .detail .phone .numbers, .card-detail-big .detail .email, .card-detail-big .detail .phone .numbers {
    font-size: 14px;
}
.card .detail .email, .card-detail-big .detail .email {
    margin-bottom: 30px;
}

.card .detail .phone, .card-detail-big .detail .phone  {
    display: flex;
    justify-content: center;
    align-items: center;
}
.card .detail .phone img, .card-detail-big .detail .phone img {
    width: 25px;
    margin-right: 10px;
}
.card .detail .phone .numbers, .card-detail-big .phone .numbers {
    color: #00a2c8;
}

@media (min-width: 768px){
    .card {
        width: 40%;
    }
    .card-detail-big{
        margin: 0 20px 0 0;
        width: 30%;
        height: 390px;
    }
    .card-detail-big .photo-wrapper {
        margin: 10px auto;
        width: 150px;
        height: 150px;
        overflow: hidden;
    }
    .card-detail-big .detail .name {
        font-size: 28px;
        font-weight: bold;
        margin: 20px 0 10px;
    }
    
}
@media (min-width: 992px){
    .card {
        width: 28%;
    }
    .card-detail-big{
        margin: 0 20px 0 0;
        width: 40%;
        height: 358px;
    }
    .card-detail-big .photo-wrapper {
        margin: 10px auto;
        width: 190px;
        height: 190px;
        overflow: hidden;
    }
    .card-detail-big .detail .name {
        font-size: 38px;
        font-weight: bold;
        margin: 20px 0 10px;
    }
    .card-detail-big .detail .email{
        font-size: 20px;
    }
    .card-detail-big .detail .phone {
        margin-top: 20px;
    }
    .card-detail-big .detail .phone img{
        width: 40px;
    }
    .card-detail-big .detail .phone .numbers{
        
        font-size: 20px;
    }
}

