.card, .card-detail-big{
    text-align: center;
    padding: 30px 10px;
    width: 70%;
    box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.17);
    border-radius: 16px;
    margin: 20px auto;
    transition: .5s ease;
    background: white;
    cursor: pointer;
}
.card-detail-big{
    width: 90%;

}

.card:hover {
    transform: translateY(-7px);
}
.card .photo-wrapper, .card-detail-big .photo-wrapper {
    margin: 10px auto;
    width: 100px;
    height: 100px;
    overflow: hidden;
    border-radius: 50%;
}
.card .photo-wrapper img ,.card-detail-big .photo-wrapper img {
    width: 100%;

}
.card .detail .name, .card-detail-big .detail .name {
    font-size: 18px;
    font-weight: bold;
    margin: 5px 0;
    text-decoration: none;
    display: block;
    color: black
}
.card .detail .email, .detail .phone .numbers, .card-detail-big .detail .email, .card-detail-big .detail .phone .numbers {
    font-size: 14px;
}
.card .detail .email, .card-detail-big .detail .email {
    margin-bottom: 30px;
}

.card .detail .phone, .card-detail-big .detail .phone  {
    display: flex;
    justify-content: center;
    align-items: center;
}
.card .detail .phone img, .card-detail-big .detail .phone img {
    width: 25px;
    margin-right: 10px;
}
.card .detail .phone .numbers, .card-detail-big .phone .numbers {
    color: #00a2c8;
}

@media (min-width: 768px){
    .card {
        width: 40%;
    }
    .card-detail-big{
        margin: 0 20px 0 0;
        width: 30%;
        height: 390px;
    }
    .card-detail-big .photo-wrapper {
        margin: 10px auto;
        width: 150px;
        height: 150px;
        overflow: hidden;
    }
    .card-detail-big .detail .name {
        font-size: 28px;
        font-weight: bold;
        margin: 20px 0 10px;
    }
    
}
@media (min-width: 992px){
    .card {
        width: 28%;
    }
    .card-detail-big{
        margin: 0 20px 0 0;
        width: 40%;
        height: 358px;
    }
    .card-detail-big .photo-wrapper {
        margin: 10px auto;
        width: 190px;
        height: 190px;
        overflow: hidden;
    }
    .card-detail-big .detail .name {
        font-size: 38px;
        font-weight: bold;
        margin: 20px 0 10px;
    }
    .card-detail-big .detail .email{
        font-size: 20px;
    }
    .card-detail-big .detail .phone {
        margin-top: 20px;
    }
    .card-detail-big .detail .phone img{
        width: 40px;
    }
    .card-detail-big .detail .phone .numbers{
        
        font-size: 20px;
    }
}

