.logo-wrapper {
  margin-bottom: 20px;
}
.logo-wrapper h1{

    font-weight:600;
    background: linear-gradient(131.06deg, #00A2C8 12.5%, #1CD3FE 93.53%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 20px;
}

@media (min-width: 768px){
  .logo-wrapper {
    margin-bottom: 5px;
  }
}